.submit-button {
    background-color: #008cff;
    border-radius: 6px;
    padding: 12px;
    outline: none;
    font-size: 18px;
    color: white;
    border: none;
    transition: 0.5s all;
    cursor: inherit;
}
.submit-button:hover:not(:disabled) {
    cursor: pointer;
}
.submit-button:disabled {
    background-color: #5cb5ff;
}